export default {
	data() {
		return {
			address:'',
			shopname:"",
			house_name:"",
			house_price:"",
			times:"",
			total_fee:"",
			door_photos:'',
			uid:''
		}
	},
	onLoad(e) {
		var oid = e.id
		this.uid = e.id
		this.conIndex(oid)
	},
	methods: {
		async conIndex(id){
			const that = this
			const res = await this.$myRequest({
				method:"POST",
				url:"/renew?oid="+id
			})
			that.address = res.data.data.address
			that.shopname = res.data.data.shopname
			that.house_name = res.data.data.house_name
			that.house_price = res.data.data.house_price
			that.times = res.data.data.times
			that.total_fee = res.data.data.total_fee
			that.door_photos = res.data.data.door_photos
		},
		zhifu(info){			
			uni.requestPayment({
				provider:"wxpay",
				timeStamp:info.timeStamp,
				nonceStr:info.nonceStr,
				package:info.package,
				signType:info.signType,
				paySign:info.paySign,
				orderInfo:info.out_trade_no,
				success:function(res){
					uni.navigateTo({
						url:"../payment_success/payment_success"
					})
				},
				fail:function(err){
					uni.navigateTo({
							url:"../payment_failure2/payment_failure2?id="+ info.out_trade_no
					})
				}
					
			})
		},
		async xuPay(){
			console.log(this.uid)
			const that = this
			const res = await this.$myRequest({
				method:"POST",
				url:"/xfpay",
				data:{
					oid:that.uid
				}
			})
			const info = res.data
			if(info.code == 0){
				uni.showToast({
					title:info.msg,
					icon:'none',
					duration:2000
				})
			} else {
				that.zhifu(info)
			}
		}
	}
}
